.message {
    display: flex;
    flex-direction: column;
    align-items: end;
    padding-bottom: 25px;
}

.imageAndText {
    display: flex;
    flex-direction: row-reverse;
    justify-content: end;
    gap: 15px;
    max-width: 50%;
    margin-bottom: 5px;
}

.imageAndText img {
    display: block;
    align-self: end;
    width: 50px;
    height: 50px;
}

.text {
    font-size: 16px;
    text-align: right;
    width: 100%;
    padding: 10px 15px;
    border-radius: 10px;
    color: #fff;
    background-color: #0066cc;
    box-shadow: 0 1px 2px rgba(29, 33, 38, 0.1);
    position: relative;
}

.text:after {
    content: '';
    position: absolute;
    right: -10px;
    bottom: 0;
    border: 10px solid transparent;
    border-bottom: 10px solid #0066cc;
}

.name {
    font-size: 18px;
    font-weight: 600;
}

.messageText {
    white-space: pre-wrap;
    word-break: break-word;
}

.time {
    font-size: 12px;
    font-weight: 600;
    margin-right: 9px;
}