.sendForm {
    display: flex;
    gap: 45px;
}

.textarea {
    width: 975px;
    width: 100%;
    padding: 11px;
    font-size: 14px;
    background-color: #f5f7fb;
    box-shadow: 0 1px 2px rgba(29, 33, 38, 0.1);
    border: 1px solid;
    border-radius: 20px;
    overflow: hidden;
    resize: none;
}

.button {
    height: 40px;
    width: 120px;
    align-self: center;
    font-size: 16px;
    font-weight: 600;
    color: #fff;
    background-color: #0066cc;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: transform .3s;
}

.button:hover {
    transform: scale(1.05);
}

.button:active {
    transform: translateY(-2px);
}
