.friendMessage {
    display: flex;
    flex-direction: column;
    align-items: start;
    padding: 20px 0;
}

.friendImageAndText {
    display: flex;
    justify-content: start;
    gap: 15px;
    max-width: 50%;
    margin-bottom: 5px;
}

.friendImageAndText img {
    display: block;
    align-self: end;
    width: 50px;
    height: 50px;
}

.friendText {
    font-size: 16px;
    width: 100%;
    padding: 10px 15px;
    border-radius: 10px;
    color: #fff;
    background-color: #0066cc;
    box-shadow: 0 1px 2px rgba(29, 33, 38, 0.1);
    position: relative;
}

.friendText:after {
    content: '';
    position: absolute;
    left: -10px;
    bottom: 0;
    border: 10px solid transparent;
    border-bottom: 10px solid #0066cc;
}

.friendName {
    font-size: 18px;
    font-weight: 600;
}

.friendMessageText {
    white-space: pre-wrap;
    word-break: break-word;
}

.friendTime {
    font-size: 12px;
    font-weight: 600;
    margin-left: 9px;
}
