.greetingForm {
}

.inputAndButtonContainer {
    display: flex;
    align-items: center;
    gap: 15px;
    position: relative;
    margin-bottom: 7px;
    :first-child{
        width: 370px;
    }
}

input {
    font-size: 16px;
    font-weight: 500;
    height: 35px;
    width: 100%;
    padding: 5px 10px;
    border-radius: 5px;
}

.error {
    position: absolute;
    bottom: -25px;
    color: #c00;
}

.input:focus {
}

.errorInput {
    border: 2px solid #c00;
}

.button {
    font-size: 14px;
    font-weight: 600;
    color: #fff;
    background-color: #0066cc;
    border: none;
    border-radius: 5px;
    width: 80px;
    height: 35px;
    cursor: pointer;
    transition: transform .3s;
}

.button:hover {
    transform: scale(1.05);
}

.button:active {
    transform: translateY(-2px);
}

.button:disabled {
    background-color: #80b3e6;
}

.text {
    margin-bottom: 10px;
    opacity: .5;
}

